@import '../../global.scss';

.contact{
    background-color: whitesmoke;
    display: flex;

    @include mobile{
        flex-direction: column;
    }

    .left{
        flex: 1;
        overflow: hidden;
        img{
            height: 89%;
        }
    }
    .right{
        flex: 1;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;

        h2{
            font-size: 30px;
            font-family: 'Times New Roman', Times, serif;
        }
        form{
            width: 70%;
            height: 70%;
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: space-around;
            
            input{
                width: 300px;
                height: 30px;
                font-size: 14px;
                font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;

                @include mobile{
                    width: 200px;
                    height: 20px;
                }
            }

            textarea{
                width: 300px;
                height: 200px;
                font-size: 14px;
                font-family:'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;

                @include mobile{
                    width: 200px;
                    height: 100px;
                }

            }
            button{
                width: 150px;
                height: 30px;
                color: white;
                background-color: red;
                font-family: 'Times New Roman', Times, serif;
                border: none;
                border-radius: 10px;
                font-weight: 500;
                cursor: pointer;

                &:focus{
                    outline: none;
                }
            }
            span{
                color:blue;
                font-family: 'Times New Roman', Times, serif;
            }
        }
    }
}